import Axios from "axios";
import Config from "../../config";

// Combining CHS Resources, Additional Resources, & Alerts
const defaultState = {
  resources: []
};
const getters = {
  RESOURCES: state => {
    return state.resources;
  }
};
const mutations = {
  SET_RESOURCES: (state, payload) => {
    state.resources = payload;
  },
  SET_ALERTS: (state, payload) => {
    state.resources.push(payload);
  }
};
const actions = {
  FETCH_RESOURCES: async context => {
    let { data } = await Axios.get(`${Config.apiRoot}/other`);
    context.commit("SET_RESOURCES", data.message);
    data = await Axios.get(`${Config.apiRoot}/alerts`);
    context.commit("SET_ALERTS", {
      category: "alerts",
      alerts: data.data.message
    });
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
