import Axios from "axios";
import Config from "../../config";

const defaultState = {
  diagnosis: []
};
const getters = {
  DIAGNOSIS: state => {
    return state.diagnosis;
  }
};
const mutations = {
  SET_DIAGNOSIS: (state, payload) => {
    state.diagnosis = payload;
  }
};
const actions = {
  FETCH_DIAGNOSIS: async context => {
    const { data } = await Axios.get(`${Config.apiRoot}/admin`);
    const sortedData = data.message.sort((a, b) => a.name.toLowerCase().localeCompare(b.name.toLowerCase())); // sort alpha
    context.commit("SET_DIAGNOSIS", sortedData);
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
