<template>
  <v-app>
    <v-toolbar app>
      <v-img
        height="30"
        max-width="30"
        contain
        src="./img/icons/chsli-logo-trans.png"
        @click="reload()"
      ></v-img>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-toolbar-title slot="extension" class="search-bar">
        <v-autocomplete
          v-model="search"
          :items="diagnosisForSearch"
          persistent-hint
          item-text="name"
          item-value="id"
          @change="itemSelection(search)"
          solo
          full-width
          prepend-icon="search"
        ></v-autocomplete>
      </v-toolbar-title>
    </v-toolbar>
    <v-content>
      <router-view/>
    </v-content>
    <v-footer height="50" fixed app>
      <v-container fluid pa-0>
        <v-layout row align-center>
          <v-flex class="text-xs-center">
            <v-icon large @click="routerSelection('home')" class="hover" color="white">home</v-icon>
          </v-flex>
        </v-layout>
      </v-container>
    </v-footer>
  </v-app>
</template>

<script>
import Router from "./router";

export default {
  name: "App",
  data() {
    return {
      title: "ICD-10 Pocket Guide",
      search: null
    };
  },
  computed: {
    diagnosis() {
      return this.$store.getters.DIAGNOSIS;
    },
    diagnosisForSearch() {
      // decode name
      return this.diagnosis.map(el => {
        return { id: el.id, name: decodeURIComponent(el.name) };
      });
    }
  },
  beforeMount() {
    this.$store.dispatch("FETCH_DIAGNOSIS");
    this.$store.dispatch("FETCH_FAVORITES");
    this.$store.dispatch("FETCH_RESOURCES");
  },
  methods: {
    routerSelection: function(sel) {
      Router.push({ name: sel });
    },
    itemSelection: function(selectedId) {
      Router.push({ name: "diagnosis", params: { id: selectedId } });
    },
    reload: function() {
      window.location.reload(true);
    }
  }
};
</script>

<style>
.search-bar {
  margin-left: 0 !important;
  margin-top: 7px !important;
  width: 100%;
}

.hover {
  cursor: pointer;
}

h1,
h2,
h3,
.v-toolbar__title,
.v-card__title,
.v-card__text,
.headline,
.return-icon {
  color: #322a7e !important;
}

.v-footer,
.v-toolbar__extension {
  background-color: #8282be !important;
}

.v-card {
  background-color: #dbdbf1 !important;
}
</style>
