import Config from "../../config";

const defaultState = {
  favorites: []
};
const getters = {
  FAVORITES: state => {
    return state.favorites;
  }
};
const mutations = {
  SET_FAVORITES: (state, payload) => {
    state.favorites = payload;
  },
  NEW_FAVORITES: (state, payload) => {
    state.favorites.push(payload);
  },
  DELETE_FAVORITES: (state, payload) => {
    state.favorites = state.favorites.filter(item => item != payload);
  }
};
const actions = {
  FETCH_FAVORITES: context => {
    const data = localStorage.getItem(Config.localStorePrefix);
    let favs;

    try {
      favs = JSON.parse(data);
      if (!favs) {
        favs = []; // for the first time
      } else if (typeof favs === "number") {
        favs = [];
        favs.push(parseInt(data, 10));
      }
    } catch (e) {
      // for backward compatibility
      favs = data.split(",");
      for (var i = 0; i < favs.length; i++) {
        favs[i] = parseInt(favs[i], 10); // convert to int
      }
    }
    //    data = check if array;
    context.commit("SET_FAVORITES", favs);
    return true;
  },
  NEW_FAVORITES: ({ commit, state }, payload) => {
    commit("NEW_FAVORITES", payload);
    localStorage.setItem(Config.localStorePrefix, JSON.stringify(state.favorites));
    return true;
  },
  DELETE_FAVORITES: ({ commit, state }, payload) => {
    commit("DELETE_FAVORITES", payload);
    localStorage.setItem(Config.localStorePrefix, JSON.stringify(state.favorites));
  }
};

export default {
  state: defaultState,
  getters,
  mutations,
  actions
};
