<template>
  <v-container fluid grid-list-lg>
    <v-layout row v-for="menu of menus" :key="menu.title">
      <v-flex>
        <v-card>
          <v-card-title class="justify-center hover" @click="routerSelection(menu.route, menu.params)">
            <v-icon v-if="menu.icon" large class="icon-yellow">star</v-icon>
              <v-badge v-if="menu.title === 'Alerts'" right color="red">
                <span slot="badge">{{ alerts }}</span>
                <h2>{{ menu.title }}</h2>
              </v-badge>
              <h2 v-if="menu.title != 'Alerts'">{{ menu.title }}</h2>
          </v-card-title>
        </v-card> 
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Router from "../router";

export default {
  name: "Home",
  data: () => ({
    menus: [
      { title: "Alerts", route: "alerts", params: {} },
      { title: "Favorites", route: "favorites", icon: "star", params: {} },
      { title: "Diagnosis", route: "diagnosis-list", params: {} },
      { title: "Additional Resources", route: "resources", params: { type: "resources" } },
      { title: "CHS Resources", route: "resources", params: { type: "chsresources" } },
      { title: "Coding Talk", route: "coding-talk", params: {} }
    ]
  }),
  computed: {
    alerts() {
      const alerts = this.$store.getters.RESOURCES.find(item => item.category === "alerts");
      if (alerts) {
        return alerts.alerts.length;
      }
      return null;
    }
  },
  methods: {
    routerSelection: function(sel, options) {
      Router.push({ name: sel, params: options });
    }
  }
};
</script>

